<template>
  <div class="container">
    <el-card class="box-card">
      <div slot="header" class="clearfix" v-if="$route.query.name">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item>学科管理</el-breadcrumb-item>
          <el-breadcrumb-item>{{ $route.query.name }}</el-breadcrumb-item>
          <el-breadcrumb-item>标签管理</el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <el-form ref="form" type="flex">
        <el-row class="el-row">
          <el-form-item label="目录名称" label-width="80px">
            <el-input v-model="directory" style="width: 210px"></el-input>
          </el-form-item>
          <el-form-item label="状态" label-width="80px">
            <el-select v-model="value" placeholder="请选择">
              <el-option label="启用" value="1"></el-option>
              <el-option label="禁用" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label-width="30px">
            <el-button size="small" :disabled="$route.query.id" @click="cleaer"
              >清除</el-button
            >
            <el-button
              size="small"
              :disabled="$route.query.id"
              @click="search"
              type="primary"
              >搜索</el-button
            >
          </el-form-item>
          <el-form-item class="btnTwo">
            <el-button
              type="text"
              icon="el-icon-back"
              v-show="$route.query.name"
              @click="$router.push('list')"
              >返回学科</el-button
            >
            <el-button
              @click="open('add', $event)"
              class="el-btn"
              type="success"
              size="small"
              ><i class="el-icon-edit" style="margin-right: 5px"></i
              >新增目录</el-button
            >
          </el-form-item>
        </el-row>
      </el-form>
      <!-- 数据条数 -->
      <el-alert
        :title="'数据一共' + total + '条'"
        type="info"
        show-icon
        :closable="false"
      ></el-alert>

      <!-- table -->
      <el-table :data="tableData" style="width: 100%">
        <el-table-column type="index" label="序号" width="80">
        </el-table-column>
        <el-table-column prop="subjectName" label="所属学科"> </el-table-column>
        <el-table-column prop="directoryName" label="目录名称">
        </el-table-column>
        <el-table-column prop="username" label="创建者"> </el-table-column>
        <el-table-column width="200px" label="创建日期">
          <template #default="{ row }">
            {{ row.addDate | parseTimeByString }}
          </template>
        </el-table-column>
        <el-table-column prop="totals" label="面试题数量"> </el-table-column>
        <el-table-column prop="state" label="状态">
          <template #default="{ row }">
            {{ row.state === 0 ? "禁用" : row.state === 1 ? "启用" : "" }}
          </template>
        </el-table-column>
        <el-table-column prop="address" label="操作">
          <template #default="{ row }">
            <el-button type="text" @click="changeState(row)">{{
              row.state === 0 ? "启用" : "禁用"
            }}</el-button>
            <el-button
              type="text"
              @click="open('edit', row)"
              :disabled="row.state === 1"
              >修改</el-button
            >
            <el-button type="text" @click="del(row)" :disabled="row.state === 1"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页器 -->
      <!-- @current-change="handleCurrentChange" -->
      <el-pagination
        style="float: right; margin: 20px"
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-sizes="[5, 10, 20, 50]"
        :page-size="pagesize"
        layout="prev, pager , next ,sizes , jumper"
        :total="total"
        hide-on-single-page
      >
      </el-pagination>
    </el-card>
    <!-- dialog -->
    <el-dialog
      width="400px"
      :title="title"
      :visible.sync="dialogFormVisible"
      @close="cancel"
    >
      <el-form :model="form" :rules="rules" ref="ruleForm">
        <el-form-item label="所属学科" prop="subject">
          <el-select v-model="form.subject" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :value="item.value"
              :label="item.label"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="目录名称" prop="name">
          <el-input
            v-model="form.name"
            style="width: 220px"
            placeholder="请输入目录名称"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="isOkAdd">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// changeState
import {
  list,
  changeState,
  add,
  detail,
  update,
  remove
} from '@/api/hmmm/directorys'
import { simple } from '@/api/hmmm/subjects'
export default {
  data () {
    return {
      SubjectId: '',
      name: '',
      total: 0,
      directory: null,
      dialogType: 'add',
      id: null,
      options: [],
      value: null,
      tableData: [],
      page: 1,
      pagesize: 10,
      dialogFormVisible: false,
      form: {
        subject: '',
        name: ''
      },
      rules: {
        name: [{ required: true, message: '请输入目录名称', trigger: 'blur' }],
        subject: [{ required: true, message: '请选择学科', trigger: 'change' }]
      }
    }
  },
  // watch: {
  //   $route: function (to, from) {
  //     // 执行需要更新的方法
  //     this.getList()
  //   }
  // },
  computed: {
    title () {
      let txt = '新增目录'
      if (this.dialogType === 'add') txt = '新增目录'
      if (this.dialogType === 'edit') txt = '修改目录'
      return txt
    }
  },
  async created () {
    this.SubjectId = this.$route.query.id
    this.name = this.$route.query.name
    // if (this.name) this.directory = this.name
    if (this.name) {
      const res = await list({
        page: this.page,
        pagesize: this.pagesize,
        directoryName: this.name
      })
      this.total = res.data.counts
      this.tableData = res.data.items
    } else {
      this.getList()
    }

    const res = await simple()
    // console.log(res)
    this.options = res.data
    // console.log(this.options)
  },
  methods: {
    search () {
      // console.log(123123)
      this.page = 1
      this.getList()
    },
    cleaer () {
      // console.log(21313123)
      this.directory = null
      this.value = null
      this.page = 1
      this.getList()
    },
    handleSizeChange (val) {
      // console.log(val)
      this.pagesize = val
      this.page = 1
      this.getList()
    },
    handleCurrentChange (val) {
      // console.log(val)
      this.page = val
      this.getList()
    },
    async isOkAdd () {
      if (this.dialogType === 'add') {
        await this.$refs.ruleForm.validate()
        // console.log(this.form.subject)
        await add({
          subjectID: this.form.subject,
          directoryName: this.form.name
        })
        this.dialogFormVisible = false
        this.$message.success('添加成功')
        this.getList()
        this.$refs.ruleForm.resetFields()
      } else {
        // console.log(this.form.name)
        await update({
          subjectID: this.form.subject,
          directoryName: this.form.name,
          id: this.id
        })
        // console.log(res)
        this.getList()
        this.dialogFormVisible = false
      }
    },
    cancel () {
      this.dialogFormVisible = false
      this.$refs.ruleForm.resetFields()
    },
    async del (row) {
      // console.log(row)
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          await remove({ id: row.id })
          // console.log(res)
          this.dialogFormVisible = false
          this.getList()
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    async open (type, row) {
      this.form.subject = ''
      this.form.name = ''
      this.dialogType = type
      this.id = row.id
      // console.log('状态::', type, '数据::', row)
      this.dialogFormVisible = true
      if (type === 'edit') {
        const res = await detail({ id: this.id })
        // console.log(res)
        this.form.subject = res.data.subjectID
        this.form.name = res.data.directoryName
      }
    },
    async changeState (row) {
      // console.log(row)
      if (row.state === 1) {
        row.state = 0
      } else {
        row.state = 1
      }
      await changeState(row)
      // console.log(res)
      this.$message.success('操作成功')
      this.getList()
    },
    async getList () {
      const res = await list({
        page: this.page,
        pagesize: this.pagesize,
        state: this.value,
        directoryName: this.directory
      })
      // console.log(res)
      this.total = res.data.counts
      this.tableData = res.data.items
      // console.log(this.tableData)
    }
  }
}
</script>

<style scoped lang="scss">
.container {
  margin: 10px;
}
.el-row {
  display: flex;
}
.el-btn {
  padding: 9px 15px;
  // float: right;
  // position: absolute;
  // right: -400px;
}

.btnTwo {
  position: absolute;
  right: 10px;
  .el-icon-back {
    font-size: 14px;
    color: #409eff;
    margin-right: 20px;
  }
}
</style>
